// Settings
// ==================================================
$hamburger-padding-x                       : 1.7rem !default;
$hamburger-padding-y                       : 2.05rem !default;
$hamburger-layer-width                     : 2.6rem !default;
$hamburger-layer-height                    : 0.3rem !default;
$hamburger-layer-spacing                   : 0.5rem !default;
$hamburger-layer-color                     : #fff !default;
$hamburger-layer-border-radius             : 0 !default;
$hamburger-hover-opacity                   : 1 !default;
$hamburger-hover-transition-duration       : 0.15s !default;
$hamburger-hover-transition-timing-function: linear !default;

// To use CSS filters as the hover effect instead of opacity,
// set $hamburger-hover-use-filter as true and
// change the value of $hamburger-hover-filter accordingly.
$hamburger-hover-use-filter: false !default;
$hamburger-hover-filter    : opacity(50%) !default;

// Types (Remove or comment out what you don’t need)
// ==================================================
$hamburger-types: (
  spring
) !default;

// Base Hamburger (We need this)
// ==================================================
@import "base";

// Hamburger types
// ==================================================
@import "types/spring";
